import { Component, h, Prop, State, Element } from '@stencil/core';

import { SubscriptionData } from "../../../global/data/commonDynamic";
import { DataLayer } from '../../../global/dataLayer';

@Component({
    tag: 'ks-order-form',
    styleUrl: "orderForm.css"
})
export class OrderForm {

    @Prop() action: string;
    @Prop() method: string;
    @Prop() destination: string;
    @Prop() button: string;
    @Prop() back: string;

    @Element() root: HTMLElement;

    @State() loading: boolean = false;

	render() {

		return(
            <form action={this.action} method={this.method} enctype="application/x-www-form-urlencoded" onSubmit={(event)=>this.Submit(event)}>
                <slot/>

                <ks-button rounded submit secondary name={this.button} loading={this.loading} tall class="formsubmit"/>
                <ks-order-form-modal back={this.back}></ks-order-form-modal>
            </form>
		);
    }

    async Submit(event: Event) {
        event.preventDefault();

        const form = this.root.querySelector("form");
        const inputs = form.querySelectorAll("ks-input-dynamic, ks-input-select, ks-input-text, ks-input-check");

        let highestPosition = Number.MAX_VALUE;

        let valid = true;
        for (let i = 0; i < inputs.length; i++) {
            const input = inputs[i] as any;
            
            if(input.closest("ks-input-dynamic *") != null) continue;

            if(input.closest("ks-order-check-section:not([active])") != null) continue;
            if(input.closest("ks-order-enable-section:not([active])") != null) continue;
            if(input.closest("*[hidden]") != null) continue;

            await input.Validate();
            if(!await input.IsValid()) {
                const inputTop = (input as HTMLElement).getBoundingClientRect().top
                    - document.body.getBoundingClientRect().top;

                if(inputTop < highestPosition)
                    highestPosition = inputTop;

                valid = false;
            }
        }

        if(valid) {
            const stringArray = new Array<string>();
            
            for (let i = 0; i < form.elements.length; i++) {
                const element = form.elements[i] as HTMLInputElement;

                if( element.nodeName === "INPUT" &&
                    element.type != "submit" &&
                    element.name != ""
                ){
                    if((element.type == "checkbox" || element.type == "radio") && !element.checked)
                        continue;

                    stringArray.push(`${element.name}=${encodeURIComponent(element.value.toString())}`);
                }

                else if(element.nodeName === "SELECT" && element.name != "") {
                    stringArray.push(`${element.name}=${encodeURIComponent(element.value.toString())}`);
                }
            }

            const formData = new FormData();
            formData.append("data", stringArray.join("&") );

            this.loading = true;

            var headers = new Headers();
            headers.append('pragma', 'no-cache');
            headers.append('cache-control', 'no-cache');

            await fetch(this.action, {
                body: formData,
                method: 'POST',
                headers: headers,
                credentials: "same-origin"
            })
            .then(response => {
                if(!response.ok)
                    throw response.status;
                else {
                    const email = form.querySelector('input[type=email]') as HTMLInputElement;
                    const subscription = form.querySelector('input[name=biuletyn]') as HTMLInputElement;

                    this.track(email?.value, subscription?.checked);
                    window.location.href = this.destination;
                }
            })
            .catch(error => {
                let message = error;

                console.log(error);

                if(!navigator.onLine)
                    message = "Brak połączenia z internetem.";

                else if(Number.isInteger(error))
                    message = `Błąd połączenia - ${error}`;

                this.ShowPrompt(message);
            });

            this.loading = false;
        }

        else {
            if('scrollBehavior' in document.documentElement.style)
                window.scroll({top: highestPosition -100, behavior: "smooth"});
            else window.scroll(0, highestPosition -100);
        }
    }

    track(email = "", subscription = false) {
        if(!email || !subscription) return;

        const data: SubscriptionData = {
            email: email,
            subscriber: true,
            place: "Zapis do newslettera | Rejestracja Zamówienie"
        }

        DataLayer.subscribe(data);
    }

    ShowPrompt(message: string) {
        const prompt = this.root.querySelector("ks-order-form-modal") as any;
        prompt.Show(message);
    }
}
